___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --color-primary: #353535;
  --bg-color: rgb(243, 242, 242);
  --text-color: #000000;
  --btn-primary-bg: #007bff;
  --btn-primary-color: #ffffff;
  --btn-dark-bg: #343a40;
  --btn-dark-color: #ffffff;
  --btn-light-bg: #f8f9fa;
  --btn-light-color: #212529;
}

/* Preloader styles */
.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: white;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
  transition: opacity 0.3s ease-out;
}

.preloader.fade-out {
  opacity: 0;
}

.preloader img {
  width: 80px;
  height: 80px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

body {
  background-color: var(--bg-color);
  color: var(--text-color);
  transition:
    background-color 0.3s ease,
    color 0.3s ease;
}

body.dark-mode {
  --bg-color: #393838;
  --text-color: #ffffff;
  --color-primary: #007bff;
  --btn-primary-bg: #0d1117;
  --btn-primary-color: #ffffff;
  --btn-dark-bg: #4a4646;
  --btn-dark-color: #ddd;
  --btn-light-bg: #2a2a2a;
  --btn-light-color: #e1e1e1;
}

/* navbar */
.navbar-nav {
  position: relative;
  flex-direction: row !important;
}

.blur {
  backdrop-filter: blur(10px);
  background-color: rgba(255, 255, 255, 0.7);
  transition: background-color 0.3s ease;
}

body.dark-mode .blur {
  background-color: rgba(30, 30, 30, 0.7);
}

body.dark-mode a {
  color: var(--btn-primary-color) !important;
}

body.dark-mode .bg-light {
  background: #262626 !important;
}

body.dark-mode .nav-pills .nav-link.active {
  background: #555b63 !important;
}
.navbar-nav .active {
  position: relative;
  font-weight: 600;
  /* text-decoration: underline; */
  color: #4caf50 !important;
}

.nav-link {
  cursor: pointer;
}

.nav-link:hover {
  text-decoration: underline;
}

.nav-pills .nav-link.active,
.nav-pills .show > .nav-link {
  color: white !important;
  background-color: var(--bs-nav-pills-link-active-bg);
}

.navbar-nav .active:hover {
  color: rgb(57, 57, 57) !important;
  text-decoration: underline;
}

/* Buttons */
.btn-primary {
  background-color: var(--btn-primary-bg);
  color: var(--btn-primary-color);
  border-color: var(--btn-primary-bg);
  transition:
    background-color 0.3s ease,
    color 0.3s ease;
}

.btn-primary:hover {
  background-color: #2a4a6f;
  border-color: #2a4a6f;
}

.btn-light {
  background-color: var(--btn-light-bg);
  color: var(--btn-light-color);
  border-color: var(--btn-light-bg);
  transition:
    background-color 0.3s ease,
    color 0.3s ease;
}

.btn-light:hover {
  background-color: #e2e6ea;
  border-color: #e2e6ea;
}

body.dark-mode .btn-primary:hover {
  background-color: #274060;
  border-color: #274060;
}

.btn-dark {
  background-color: var(--btn-dark-bg);
  color: var(--btn-dark-color);
  border-color: var(--btn-dark-bg);
  transition:
    background-color 0.3s ease,
    color 0.3s ease;
}

.btn-dark:hover {
  background-color: #333333;
  border-color: #333333;
}

body.dark-mode .btn-dark:hover {
  background-color: #111111;
  border-color: #111111;
}

body.dark-mode .btn-light {
  background-color: var(--btn-light-bg);
  color: var(--btn-light-color);
  border-color: var(--btn-light-bg);
}

body.dark-mode .btn-light:hover {
  background-color: #1f1f1f;
  border-color: #1f1f1f;
}

body.dark-mode .text-muted,
body.dark-mode .form-text {
  color: var(--btn-dark-color) !important;
}

body.dark-mode .project {
  box-shadow: 0 0 10px rgb(255 255 255 / 24%);
}

.grid-container {
  display: grid;
  grid-template-columns: 250px 1fr;
  grid-template-areas: "left main";
}

.grid-container > aside {
  grid-area: left;
}

.grid-container > div:nth-of-type(1) {
  grid-area: main;
  margin-top: 53px;
}

.project {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  padding: 10px;
  border-radius: 20px;
  align-items: baseline;
}

.project p {
  margin: 0;
}

.low {
  border-left: 10px solid green;
}
.medium {
  border-left: 10px solid rgb(253, 216, 3);
}
.high {
  border-left: 10px solid red;
}

.pt-53 {
  padding-top: 53px;
}
.pt-100 {
  padding-top: 100px;
}
.pb-100 {
  padding-bottom: 100px;
}
.mb-50 {
  margin-bottom: 50px;
}

.mt-100 {
  margin: 100px;
}
.top-8 {
  top: 8%;
}
